<template>
    <div>
        <div class="sentList" v-for="item in sentLists" :key="item.id">
            <span>{{item.fileName}}</span>
            <el-progress :percentage="Number(item.documentProgress)" :format="format" v-if="!item.isAcept"></el-progress>
            <el-progress :percentage="0" :format="formats" v-else></el-progress>
        </div>
    </div>
</template>

<script>
export default {
    components: {
        
    },
    watch:{
        '$store.state.sentLists': {
            handler(newVal, oldVal) {
                console.log(newVal);
                
                this.sentLists=newVal
            },
            deep: true
        }
    },
    data() {
        return {
            sentLists:[]
        };
    },
    created() {
    },
    mounted() {
        
    },
    computed: {
        
    },
    methods: {
         format(percentage) {
            return percentage === 100 ? '完成' : `${percentage}%`;
        },
        formats() {
            return '已拒绝'
        }
    },
}
</script>
<style scoped>
.sentList{
    padding: 10px;
    width:20%;
}
</style>


