<template>
    <div>
        <el-menu :default-active="activeIndex"
            class="el-menu-demo"
            mode="horizontal"
            background-color="#545c64"
            text-color="#fff"
            active-text-color="#ffd04b"
            @select="handleSelect"
        >
            <el-menu-item index="1">上传文件</el-menu-item>
            <el-menu-item index="2">发送列表</el-menu-item>
            <el-menu-item index="3">接收列表</el-menu-item>
        </el-menu>
        <UploadFiles v-show="activeIndex==1"></UploadFiles>
        <SentList v-show="activeIndex==2"></SentList>
        <ReceptionList v-show="activeIndex==3" :datas="reception" @refuse="refuse" :websockets="ws"></ReceptionList>
    </div>
</template>

<script>
import UploadFiles from "@/components/UploadFiles";
import ReceptionList from "@/components/ReceptionList";
import SentList from "@/components/SentList";
export default {
    components: {
        UploadFiles,
        ReceptionList,
        SentList
    },
    data() {
        return {
            activeIndex:"1",
            ws:null,
            reception:[]
        };
    },
    created() {
        this.ws = new WebSocket("ws://"+window.location.host+",/upload/1"); 
        var that=this
        if(this.ws.readyState == 0){
            
            this.ws.onopen = function(){
                console.log("open");
            }
        }
        
        this.ws.onmessage=function (evt) {
            let newSentList=JSON.parse(JSON.stringify(that.$store.state.sentLists))
            let datas=JSON.parse(evt.data)
            console.log(datas);

            if(datas.isAcept==="1"){
                console.log(1);
                    console.log("newSentList",newSentList);
                if(newSentList.length){
                        for (let i = 0; i < newSentList.length; i++) {
                            if(newSentList[i].fileName===datas.fileName){
                                newSentList[i].isAcept=datas.isAcept
                            }
                        }
                        that.$store.commit('edit',newSentList)
                    }
            }else{
                if(datas.isSend==="1"){
                    if(newSentList.length){
                        for (let i = 0; i < newSentList.length; i++) {
                            if(newSentList[i].fileName===datas.fileName){
                                newSentList[i].documentProgress=datas.documentProgress
                            }
                        }
                        that.$store.commit('edit',newSentList)
                    }
                }else{
                    if(that.reception.length){
                        let pushs=0
                        for (let i = 0; i < that.reception.length; i++) {
                            if(that.reception[i].fileName===datas.fileName){
                                pushs=1
                                that.reception[i].documentProgress=datas.documentProgress
                                if(datas.path){
                                    var newStr = datas.path.slice(0,5)+'/'+datas.path.slice(5)
                                    
                                    that.reception[i].path=newStr
                                    that.$forceUpdate()
                                }
                            }
                        }
                        if(!pushs){
                            that.reception.unshift(datas)
                        }
                    }else{
                        that.reception.push(datas)
                    }
                }
        }
    }
    },
    mounted() {
        
    },
    computed: {
        
    },
    methods: {
        refuse(msg){
            let arrs=JSON.parse(JSON.stringify(this.reception))
            this.reception.length=0
            for (let i = 0; i < arrs.length; i++) {
                if(arrs[i].fileName===msg.fileName){
                    arrs[i].isAcpets=1
                }
            }
            this.reception=arrs
            
        },
        handleSelect(index){
            this.activeIndex=index
            if(index==2){
                this.getSentList()
            }
        },
        getSentList(){
            var parame={
                method: 'get',
                url:"/fileList"
            }
            var that=this
            this.$axios(parame).then(function (res) {
                that.$store.commit('edit',res.data.data)
                
            }).catch(function (error) {
                console.log(error)
            })
        }
    },
    destroyed(){
        this.ws.onclose = function(e){
            console.log("close");
        }
    }
}
</script>
<style scoped>

</style>


