<template>
    <div>
        <div class="sentList" v-for="item in dataList" :key="item.id">
            <div class="sentCount">
                <p>{{item.file.fileName}}</p>
                <el-progress :percentage="Number(item.documentProgress)" :format="format"></el-progress>
            </div>
            <div v-if="Number(item.documentProgress)!==100">
                <div v-if="item.isAcpets">
                    <el-button type="danger" disabled>已拒绝</el-button>
                </div>
                <div v-else>
                    <el-button type="primary" @click="reception(item.file)">接收</el-button>
                    <el-button type="danger" @click="refuse(item)">拒绝</el-button>
                </div>
            </div>
            <div v-else>
                <a :href="item.path" :download="item.file.fileName"><el-button type="info" :disabled="Number(item.documentProgress)!==100" @click="downLoad(item.path)">下载</el-button>
                </a>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props:{
        datas: {
            type: Array,
        },
        websockets:{}
    },
    watch:{
        datas(newValue){
            console.log("newValue",newValue);
            
            this.dataList=newValue
        }
    },
    components: {
        
    },
    data() {
        return {
            dataList:null,
            isAcpet:1
        };
    },
    created() {
        
    },
    methods: {
        downLoad(url){
            window.open(url)
        },
        reception(file){
             let fileJson = {
                type: "1",
                data:(file)
            };
            this.websockets.send(JSON.stringify(fileJson));
        },
        refuse(item){
            let fileJson = {
                type: "2",
                data:(item.file)
            };
            this.websockets.send(JSON.stringify(fileJson));
            this.$emit("refuse",item)
        },
         format(percentage) {
            return percentage === 100 ? '完成' : `${percentage}%`;
        }
    },
}
</script>
<style scoped>
.sentList{
    padding: 10px;
    display: flex;
    width: 40%
}
.sentCount{
    width: 50%
}
</style>


