import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex)


const store = new Vuex.Store({
    state: {
        sentLists: []
    },
    mutations: {
        edit(state, sentLists) {
            state.sentLists = sentLists
        }
    }
})

export default store


