<template>
    <div>
        <el-upload
            ref="upload"
            action="kaili-basic/v1/IMei/import"
            accept='.xlsx,.xls'
            :multiple="false"
            :http-request="uploadFile"
            :limit="1"
            :auto-upload="false"
        >
            <div  style="display:flex;align-items:center">
                <div class="uploadButton"><img src="@/assets/upload.png" alt="" class="">点击上传</div>
            </div>
        </el-upload>
          <el-button type="primary" @click="submitUpload">发送</el-button>
    </div>
</template>

<script>
import { Loading } from 'element-ui';

export default {
    components: {
        
    },
    data() {
        return {
            loadingInstance:null
        };
    },
    created() {
        
    },
    mounted() {
        
    },
    computed: {
        
    },
    methods: {
        uploadFile(p){
               var url="/uploadFile"
                this.uploads(p.file,url).then((res)=>{
                    if(res.status===200){
                        this.$message.success('上传成功');
                        this.$refs.upload.clearFiles();
                    }
                })
        },
        submitUpload(){
            this.$refs.upload.submit();
        },
        uploads(file,url){
            
            const formData = new FormData();
            formData.append("file", file);
            // 请求
            const parame={
                method: 'post',
                url,
                data:formData
            }
            var that = this;
            return new Promise((resolve,reject)=>{
                // that.loadingInstance = Loading.service({
                //     lock: true,
                //     text: '正在上传请稍等',
                //     spinner: 'el-icon-loading',
                //     background: 'rgba(0, 0, 0, 0.7)'
                // });
                this.$axios(parame).then(function (res) {
                    resolve(res)
                }).catch(function (error) {
                    resolve(error)
                })
                // that.$store.commit("edit", that.loadingInstance)
                // that.$request(parame).then((res)=>{
                //     resolve(res.data)
                // })
            })
        }
    },
}
</script>
<style scoped>
.uploadButton{
    width: 130px;
    height: 40px;
    background: #E0EDFF;
    border-radius: 0.1rem;
    color: #0062FF;
    display: flex;
    justify-content: center;
    align-items: center;
    margin:  0 0.125rem 0 0.3625rem;
    cursor: pointer;
}
.uploadButton img{
    width: 16px;
    height: 15px;
    margin-right: 11px
}
</style>


